<template>
    <div id="sender">
        <el-table v-loading="loading" ref="multipleTable" :data="data" style="width: 100%"
            @select-all="handleSelectAll" @selection-change="handleSelectionChanged"   >
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column property="surname" label="" ></el-table-column>
            <el-table-column property="name" label="" ></el-table-column>
            <el-table-column property="patro" label="" ></el-table-column>
            <el-table-column property="phone" label="" ></el-table-column>
            <el-table-column property="email" label="Email" ></el-table-column>
            <el-table-column property="level" label="" ></el-table-column>
            <el-table-column property="address" label="" ></el-table-column>
        </el-table>
        <div style="margin-top: 20px">
            <el-button @click="sendEmail"> </el-button>
            <el-button @click="startCampaign"> </el-button>
        </div>
    </div>
</template>

<script>
    import {http} from "@/api/common";

    export default {
        name: "Sender",
        data() {
            return {
                data: [],
                selection: [],
                forSend: [],
                loading:true,
                addressbookId: "",
                fields: {
                    surname: {label: "", type: 'string'},
                    name: { label: "", type: 'string',},
                    patro: {label: "", type: 'string'},
                    level: {label: "", type: 'integer'},
                    phone: {label: "", type: 'string', tableHidden:true},
                    email: {label: "", type: 'string', tableHidden:true},
                    vk: {label: "", type: 'string'},
                }
            }
        },
        created() {
            http.get('/contacts-original/').then(({data}) => {
                this.data = data
                this.loading = false
            })
        },
        methods:{
            handleSelectAll(){},
            handleSelectionChanged(selection) {
                this.selection = selection
            },
            sendEmail(){
                this.forSend = [];
                if(this.selection.length>0){
                    this.selection.forEach(({id, email, name, surname}) => {
                        this.forSend.push({email: email, variables: {
                                name: name + ' ' + surname,
                                // for more variables
                            }})
                    })
                    http.post('/send-emails/', this.forSend).then((result) => {
                        this.addressbookId = result.data
                        console.log(result)
                    }).catch((error) => {
                        console.log(error)
                    })
                } else {
                    this.$alert('   ')
                }
            },
            getAddressboolAviable(){

            },
            startCampaign(){
                http.post('/get-addressbook-aviable/', {addressbookId: this.addressbookId}).then(({data}) => {
                    data = data[0]
                    if(data.status == 0){
                        http.post('/start-campaign/', {emails: this.forSend, addressbookId: this.addressbookId}).then(({he}) => {
                            console.log('good', he)
                        })
                    } else {
                        this.$alert(data.status_explain)
                    }
                })
            },
        }
    }
</script>

